import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function revenueEstimated(tableName: string) {
  const title = "Revenue Estimated"
  return genericColumn({
    endpoint: "companyLatestSurprise",
    columnKeys: ["revenueEstimated"],
    size: 120,
    title,
    enableSorting: true,
    enableHiding: true,
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip="Explain the Estimated Revenue"
          queryResult={info.row.original.financialData.companyLatestSurprise}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `Who has recently released Revenue estimates for ${info.row.original.entityInfo.name}?`,
              value: info.row.original.financialData.companyLatestSurprise?.data?.revenueEstimated,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyLatestSurprise?.data?.revenueEstimated}
        />
      )
    },
  })
}

export { revenueEstimated }
